<template>
    <div>
        <!-- 选择车辆 -->
        <el-form ref="filters" :inline="true" :model="filters">
            <el-form-item label="车辆品牌" v-if="selectCarOptions.formConfig.brand == true">
                <el-select v-model="filters.value" placeholder="请选择">
                    <el-option
                        v-for="item in options"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="车辆系列" v-if="selectCarOptions.formConfig.series == true">
                <el-select v-model="filters.value" placeholder="请选择">
                    <el-option
                        v-for="item in options"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="公告型号" v-if="selectCarOptions.formConfig.notice == true">
                <el-select v-model="filters.value" placeholder="请选择">
                    <el-option
                        v-for="item in options"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="车辆类型" v-if="selectCarOptions.formConfig.type == true">
                <el-select v-model="filters.value" placeholder="请选择">
                    <el-option
                        v-for="item in options"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="驱动形式" v-if="selectCarOptions.formConfig.drive == true">
                <el-select v-model="filters.value" placeholder="请选择">
                    <el-option
                        v-for="item in options"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="车牌号" v-if="selectCarOptions.formConfig.plate_number == true">
                <el-input v-model.trim="filters.value" placeholder="请输入"></el-input>
            </el-form-item>
        </el-form>
        <el-table :data="tableData" border @selection-change="handleSelectionChange">
            <el-table-column
                v-if="selectCarOptions.isCheckbox"
                type="selection"
                width="55">
            </el-table-column>
            <el-table-column label="" v-else>
                <template slot-scope="{row,$index}">
                    <el-radio v-model="radioActive":label="$index+1" @change="handleSelectCar(row,$index)"></el-radio>
                </template>
            </el-table-column>
            <el-table-column prop="plate_number" label="车牌号" v-if="selectCarOptions.tableConfig.plate_number == true">
            </el-table-column>
            <el-table-column prop="vin_number" label="vin" v-if="selectCarOptions.tableConfig.vin_number == true">
            </el-table-column>
            <el-table-column label="车辆信息" v-if="selectCarOptions.tableConfig.car_info == true">
                <template slot-scope="{row, $index}">
                    {{`${row.car_brand} ${row.car_series} ${row.horse_power} ${row.drive_form} ${row.car_class}`}}
                </template>
            </el-table-column>
            <el-table-column prop="band_name" label="车主名称" v-if="selectCarOptions.tableConfig.band_name == true">
            </el-table-column>
            <el-table-column prop="drive_license_number" label="行驶证号" v-if="selectCarOptions.tableConfig.drive_number == true">
            </el-table-column>
            <el-table-column prop="bus_brand" label="车辆品牌" v-if="selectCarOptions.tableConfig.bus_brand == true">
            </el-table-column>
            <el-table-column prop="bus_type" label="车辆类型" v-if="selectCarOptions.tableConfig.bus_type == true">
            </el-table-column>
            <el-table-column prop="bus_series" label="车辆系列" v-if="selectCarOptions.tableConfig.bus_series == true">
            </el-table-column>
            <el-table-column prop="bus_series" label="颜色" v-if="selectCarOptions.tableConfig.bus_series == true">
            </el-table-column>
            <el-table-column prop="color" label="颜色" v-if="selectCarOptions.tableConfig.yanse == true">
            </el-table-column>
            <el-table-column prop="foreign_model" label="公告型号" v-if="selectCarOptions.tableConfig.gonggao == true">
            </el-table-column>
            <el-table-column prop="sale_price" label="销售单价" v-if="selectCarOptions.tableConfig.danjia == true">
            </el-table-column>
            <el-table-column prop="drive_license_number" label="存量" v-if="selectCarOptions.tableConfig.cunliang == true">
            </el-table-column>
        </el-table>
        <div class="pagination">
            <el-pagination background @current-change="handleCurrentChange" layout="prev, pager, next" :total="total" :page-size="limit">
            </el-pagination>
        </div>
        <div class="ta-r pt10">
            <el-button type="primary" @click="$router.push({path: '/'})" v-if="selectCarOptions.addCar == true">添加车辆</el-button>
            <el-button @click="$emit('emitCarClose', false)">取 消</el-button>
            <el-button type="primary" @click="handleSubmit()">完 成</el-button>
        </div>
    </div>
</template>

<script>
    import { apiSelectCar } from '@/api/trafficManagement'
    import { salesManagementGetGoods } from '@/api/salesManagement'
    import * as API from '@/api/resourceManagement'
    export default {
        props:{
            selectCarOptions:{
                type: Object,
                default: {
                    tableConfig: {
                        car_info: false,
                        band_name: false,
                        plate_number: false,
                        vin_number: false,
                        bus_brand: false,
                        bus_type: false,
                        bus_series: false,

                        yanse: false,
                        gonggao: false,
                        danjia: false,
                        cunliang: false,
                    },
                    formConfig: {
                        type: false,
                        brand: false,
                        series: false,
                        notice: false,
                        drive: false,
                        plate_number: false,
                    },
                    addCar: false,
                    isCheckbox: false,
                    pageType: 'carTypeSelect'
                }
            }
        },
        data() {
            return {
                tableData: [{
                    car_brand:'',
                    car_series:'',
                    horse_power:'',
                    drive_form:'',
                    car_class:'',
                }],
                page: 1,
                total: 1,
                limit: 10,
                radioActive: 0,
                multipleSelection: [],
                filters: {
                    movieID:'',
                    movieName:'',
                    value:'3'
                },
                options: [{
                    value: '1',
                    label: '黄金糕'
                }, {
                    value: '2',
                    label: '双皮奶'
                }, {
                    value: '3',
                    label: '蚵仔煎'
                }, {
                    value: '4',
                    label: '龙须面'
                }, {
                    value: '5',
                    label: '北京烤鸭'
                }]
            }
        },
        created() {
            
        },
        mounted(){
            this.getFormData();
        },
        computed: {

        },
        methods: {
            // 分页导航
            handleCurrentChange(val) {
                this.page = val;
                this.getFormData();

            },
            getFormData() {
                let params = {
                    ...this.filters,
                    page: this.page
                }
                if(this.selectCarOptions.pageType == 'carTypeSelect'){
                    API.apiGoodsParamsCarType(params).then(res => {
                        this.tableData = res.data;
                        this.total = res.total;
                    })
                }else if(this.selectCarOptions.pageType == 'insuranceOrder'){
                    API.apiSelectCar(params).then(res => {
                        this.tableData = res.data;
                        this.total = res.total;
                    })
                }else if(this.selectCarOptions.pageType == 'salesManagement'){
                    salesManagementGetGoods(params).then(res => {
                        this.tableData = res.data;
                        this.total = res.total;
                    })
                }
            },
            handleSelectCar(row,index){

            },
            handleSelectionChange(val) {
                this.multipleSelection = val;
            },
            handleSubmit(){
                if(this.selectCarOptions.isCheckbox){
                    /*this.multipleSelection.map(item => {
                        return Object.assign(item, {
                            num: 1
                        })
                    })*/
                    this.$emit('emitCarSubmit', this.multipleSelection)
                }else{
                    this.$emit('emitCarSubmit', this.tableData[this.radioActive-1])
                }
            }
        }
    }

</script>
